import React from "react";
import {Navbar, Footer, MobileToolbar} from "./index";
import {Link} from "react-router-dom";

function ContactUs() {
    return (
        <div>
            <Navbar/>
            <MobileToolbar/>
            <header className="contact-us-header d-flex flex-column py-3">
                <div>
                    <h1 className="text-center mb-4 fw-bolder">  </h1>
                </div>
                <div>
                    <div className="text-center fs-11">
                        {<Link to="/"></Link>} /<span className="text-muted">   </span>
                    </div>
                </div>
            </header>
            <main className="bg-white py-4">
                <div className="form-container container d-flex flex-column flex-md-row">
                    <form className="ms-md-3 col-12 col-md-6">
                        <div>
                            <p>  </p>
                            <h4 className="fw-bold fs-5">      </h4>
                        </div>
                        <div className="d-flex flex-column flex-md-row gap-2">
                            <div className="col-12 col-md-6">
                                <div>
                                    <label className="fs-14" htmlFor="userName"> </label>
                                </div>
                                <input className="w-100 px-2 py-1 my-2 border rounded-2 input-font" type="text"
                                       id="userName" required={true}/>
                            </div>
                            <div className="col-12 col-md-6">
                                <div>
                                    <label className="fs-14" htmlFor="userEmail"> </label>
                                </div>
                                <input className="w-100 px-2 py-1 my-2 border rounded-2 input-font" type="email"
                                       id="userEmail" required={true}/>
                            </div>
                        </div>
                        <div className="d-flex flex-column flex-md-row gap-2">
                            <div className="col-12 col-md-6">
                                <div>
                                    <label className="fs-14" htmlFor="userNumber"> </label>
                                </div>
                                <input className="w-100 px-2 py-1 my-2 border rounded-2 input-font" type="number"
                                       id="userNumber" required={true}/>
                            </div>
                            <div className="col-12 col-md-6">
                                <div>
                                    <label className="fs-14" htmlFor="userCompany"></label>
                                </div>
                                <input className="w-100 px-2 py-1 my-2 border rounded-2 input-font" type="text"
                                       id="userCompany" required={false}/>
                            </div>
                        </div>
                        <div className="d-flex flex-column">
                            <div className="col-12">
                                <div>
                                    <label className="fs-14" htmlFor="userWebsite">  </label>
                                </div>
                                <input className="w-100 px-2 py-1 my-2 border rounded-2 input-font" type="text"
                                       id="userWebsite"/>
                            </div>
                            <div className="col-12">
                                <div>
                                    <label className="fs-14" htmlFor="userMessage"> </label>
                                </div>
                                <textarea className="w-100 px-2 py-1 my-2 border rounded-2 input-font"
                                          id="userMessage"/>
                            </div>
                        </div>
                        <div className="my-2">
                            <button className="btn bg-secondary-subtle"></button>
                        </div>
                    </form>
                    <div className="me-md-3 col-12 col-md-6">
                        <div className="questions-container">
                            <div>
                                <p>  </p>
                                <h4 className="fw-bold fs-5"> </h4>
                            </div>
                            <div className="questions">
                                <div className="accordion" id="accordionExample">
                                    <div className="accordion-item">
                                        <h2 className="accordion-header" id="headingOne">
                                            <button className="accordion-button" type="button" data-bs-toggle="collapse"
                                                    data-bs-target="#collapseOne" aria-expanded="true"
                                                    aria-controls="collapseOne">
                                                <span>       </span>
                                            </button>
                                        </h2>
                                        <div id="collapseOne" className="accordion-collapse collapse show"
                                             aria-labelledby="headingOne" data-bs-parent="#accordionExample">
                                            <div className="accordion-body">
                                                <p>            
                                                    
                                                             .</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="accordion-item">
                                        <h2 className="accordion-header" id="headingTwo">
                                            <button className="accordion-button collapsed" type="button"
                                                    data-bs-toggle="collapse" data-bs-target="#collapseTwo"
                                                    aria-expanded="false" aria-controls="collapseTwo">
                                                <span>   </span>
                                            </button>
                                        </h2>
                                        <div id="collapseTwo" className="accordion-collapse collapse"
                                             aria-labelledby="headingTwo" data-bs-parent="#accordionExample">
                                            <div className="accordion-body">
                                                <p>  : <a href="tel:09120000000">09120000000</a></p>
                                                <p> :<a
                                                    href="mailto:something@example.com"> example@something.com</a>
                                                </p>
                                                <p className="mb-0"> :     100</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="accordion-item">
                                        <h2 className="accordion-header" id="headingThree">
                                            <button className="accordion-button collapsed" type="button"
                                                    data-bs-toggle="collapse" data-bs-target="#collapseThree"
                                                    aria-expanded="false" aria-controls="collapseThree">
                                                <span>      </span>
                                            </button>
                                        </h2>
                                        <div id="collapseThree" className="accordion-collapse collapse"
                                             aria-labelledby="headingThree" data-bs-parent="#accordionExample">
                                            <div className="accordion-body">
                                                <p>            
                                                              
                                                    .</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="accordion-item">
                                        <h2 className="accordion-header" id="headingFour">
                                            <button className="accordion-button collapsed" type="button"
                                                    data-bs-toggle="collapse" data-bs-target="#collapseFour"
                                                    aria-expanded="false" aria-controls="collapseFour">
                                                <span>        </span>
                                            </button>
                                        </h2>
                                        <div id="collapseFour" className="accordion-collapse collapse"
                                             aria-labelledby="headingFour" data-bs-parent="#accordionExample">
                                            <div className="accordion-body">
                                                <p>         .  
                                                    
                                                               
                                                    
                                                      .</p>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="accordion-item">
                                        <h2 className="accordion-header" id="headingFive">
                                            <button className="accordion-button collapsed" type="button"
                                                    data-bs-toggle="collapse" data-bs-target="#collapseFive"
                                                    aria-expanded="false" aria-controls="collapseFive">
                                                <span>      </span>
                                            </button>
                                        </h2>
                                        <div id="collapseFive" className="accordion-collapse collapse"
                                             aria-labelledby="headingFive" data-bs-parent="#accordionExample">
                                            <div className="accordion-body">
                                                <p>       .     
                                                               
                                                     .          
                                                    .</p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </main>
            <Footer/>
        </div>

    )
}

export default ContactUs;